<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-6">
        <h3 class="text-center">비밀번호 찾기({{ $route.params.level }})</h3>
        <form @submit.prevent="findMyPw">
          <div class="form-group row">
            <label for="memail" class="col-sm-4 col-form-label">아이디</label>
            <div class="col-sm-11">
              <input
                type="email"
                class="form-control"
                id="memail"
                v-model="memail"
                required
                placeholder="아이디를 입력하세요"
              />
            </div>
          </div>
          <div class="form-group row">
            <label for="mtel" class="col-sm-4 col-form-label">연락처</label>
            <div class="col-sm-11">
              <input
                type="tel"
                v-model="mtel"
                class="form-control"
                id="mtel"
                pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
                required
                placeholder="예시: 010-1234-5678"
              />
            </div>
          </div>
          <div class="form-group row">
            <div class="col-sm-12 text-center">
              <button type="submit" class="btn btn-primary">확인</button>
            </div>
          </div>
        </form>
        <div class="resultMessage">
          <div v-if="myPwFound !== null && myPwFound !== ''">
            당신의 암호는 {{ myPwFound }} 입니다. 로그인 후에 암호를 바로
            수정해주세요.
          </div>
          <div v-else-if="myPwFound === ''">
            해당하는 정보를 찾을 수 없습니다.
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
import axios from "axios";

export default {
  data() {
    return {
      memail: "",
      mtel: "",
      myPwFound: null,
    };
  },
  // mounted() {
  //   // 가져온 사용자 레벨 값으로 로직 수행
  //   console.log("사용자 레벨:", this.$route.params.level);
  // },
  methods: {
    findMyPw() {
      axios
        .post("/findMyPw", {
          memail: this.memail,
          mtel: this.mtel,
        })

        .then((response) => {
          // 서버로부터의 응답 처리
          this.myPwFound = response.data ? response.data : "";
        })
        .catch((error) => {
          // 에러 처리
          console.error("Error:", error);
          this.myPwFound = ""; // 에러가 발생했을 때 처리
        });
    },
  },
};
</script>
  
  <style scoped>
.form-group {
  margin-top: 20px;
  margin: bottom 20px;
}

.btn {
  margin-top: 10px;
  margin-bottom: 5px;
  color: black;
  background-color: #f5beae;
  border-color: #f5beae;
}

.btn:hover {
  background-color: #f5835d;
}

.resultMessage {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  margin-top: 20px;
}
</style>